<template>
  <div>
    <button type="button" v-on:click ="showMenu();">사용자메뉴</button>
    <button v-on:click ="showMenuAdmin();">관리자메뉴</button>
  </div>
  <div v-if="menu">
      <Menu />
      <DigitalClock />
  </div>
  <div v-if="menu_admin">
      <MenuAdmin />
      <DigitalClock />
  </div>
</template>

<script>

import Menu from './Menu';
import MenuAdmin from './MenuAdmin';
import DigitalClock from './DigitalClock.vue';

export default {
  name: 'Left',
  data() {
    return {
            menu: true, 
            menu_admin: false,
    }
  },
  components: {
    Menu: Menu,
    MenuAdmin: MenuAdmin,
    DigitalClock, 
  },
  methods: {
    showMenu(){
      // alert('showMenu');
      this.menu=true;
      this.menu_admin=false;
    }, 
    showMenuAdmin(){
      // alert('showMenuAdmin');
      this.menu=false;
      this.menu_admin=true;
    }
  }
}
</script>
